<template>
  <div id="parameterSettings">
    <div>
      <Form ref="parameter" :model="parameterData" :rules="ruleValidate" :label-width="145" >
        <FormItem prop="redLineUrl" label="红线斑块服务地址：">
          <Input v-model.trim="parameterData.redLineUrl" placeholder="请输入红线斑块服务地址" size="default" ><Button slot="append" @click="save(1)" type="primary">保存</Button></Input>
        </FormItem>
        <FormItem prop="pointUrl" label="预设拐点服务地址：">
          <Input v-model.trim="parameterData.pointUrl" placeholder="请输入预设拐点服务地址" size="default"><Button slot="append" @click="save(4)" type="primary">保存</Button></Input>
        </FormItem>
        <FormItem prop="boundaryMarkersUrl" label="预设界桩服务地址：">
          <Input v-model.trim="parameterData.boundaryMarkersUrl" placeholder="请输入预设界桩服务地址" size="default"><Button slot="append" @click="save(2)" type="primary">保存</Button></Input>
        </FormItem>
        <FormItem prop="signUrl" label="预设标识牌服务地址：">
          <Input v-model.trim="parameterData.signUrl" placeholder="请输入预设标识牌服务地址" size="default"><Button slot="append" @click="save(3)" type="primary">保存</Button></Input>
        </FormItem>
        <FormItem prop="vectorMapUrl" label="矢量地图服务地址：">
          <Input v-model.trim="parameterData.vectorMapUrl" placeholder="请输入矢量地图服务地址" size="default"><Button slot="append" @click="save(5)" type="primary">保存</Button></Input>
        </FormItem>
        <FormItem prop="imageMapsUrl" label="影像地图服务地址：">
          <Input v-model.trim="parameterData.imageMapsUrl" placeholder="请输入影像地图服务地址" size="default"><Button slot="append" @click="save(6)" type="primary">保存</Button></Input>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
import {
  // 获取服务地址
  getNewList,
  // 保存服务地址
  saveServerList
} from '@/api/systemManage'
export default {
  name: 'parameterSettings',
  data () {
    return {
      /* 数据表单参数 */
      parameterData: {
        redLineUrl: '',
        pointUrl: '',
        boundaryMarkersUrl: '',
        signUrl: '',
        vectorMapUrl: '',
        imageMapsUrl: ''
      },
      /* 数据表单校验 */
      ruleValidate: {
        redLineUrl: [
          { required: true, message: '红线斑块服务地址必须填写', trigger: 'blur' }
        ],
        pointUrl: [
          { required: true, message: '预设拐点服务地址必须填写', trigger: 'blur' }
        ],
        boundaryMarkersUrl: [
          { required: true, message: '预设界桩服务地址必须填写', trigger: 'blur' }
        ],
        signUrl: [
          { required: true, message: '预设标识牌服务地址必须填写', trigger: 'blur' }
        ],
        vectorMapUrl: [
          { required: true, message: '矢量地图服务地址必须填写', trigger: 'blur' }
        ],
        imageMapsUrl: [
          { required: true, message: '影像地图服务地址必须填写', trigger: 'blur' }
        ]
      }
    }
  },
  components: {},
  mounted () {
    this.getData()
  },
  methods: {
    /* 获取数据地址的接口 */
    getData () {
      getNewList({
        data: {}
      }).then(res => {
        if (res.data.code === '0000') {
          res.data.data.forEach(val => {
            switch (val.type) {
              case 1:
                this.parameterData.redLineUrl = val.serviceUrl
                break
              case 2:
                this.parameterData.boundaryMarkersUrl = val.serviceUrl
                break
              case 3:
                this.parameterData.signUrl = val.serviceUrl
                break
              case 4:
                this.parameterData.pointUrl = val.serviceUrl
                break
              case 5:
                this.parameterData.vectorMapUrl = val.serviceUrl
                break
              case 6:
                this.parameterData.imageMapsUrl = val.serviceUrl
                break
              default:
                break
            }
          })
        } else {
          this.$Notice.error({ title: '获取参数设置信息失败', desc: res.data.msg })
        }
      })
    },
    save (type) {
      // 存储服务地址
      let url = ''
      switch (type) {
        case 1:
          url = this.parameterData.redLineUrl
          break
        case 2:
          url = this.parameterData.boundaryMarkersUrl
          break
        case 3:
          url = this.parameterData.signUrl
          break
        case 4:
          url = this.parameterData.pointUrl
          break
        case 5:
          url = this.parameterData.vectorMapUrl
          break
        case 6:
          url = this.parameterData.imageMapsUrl
          break
        default:
          break
      }
      saveServerList({
        data: {
          type,
          url
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '参数设置成功' })
        } else {
          this.$Notice.error({ title: '获取', desc: res.data.msg })
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  #parameterSettings {
    .ivu-form {
      width: 50%;
      margin: 4% auto 0;
    }
    .btn-cancel {
      border: 1px solid #d7dde4;
      background: #d7dde4;
      margin-left: 20px;
      &:hover {
        color: #57a3f3;
        background-color: #fff !important;
        border-color: #57a3f3 !important;
      }
    }
    .mt50 {
      margin-top: 5%;
    }
  }
</style>
